import React, { Fragment, useEffect, useState } from 'react';
import axios from 'axios';
import { Map, Marker, Popup, TileLayer, Polyline } from 'react-leaflet';
import { divIcon } from 'leaflet';

import differenceInDays from 'date-fns/difference_in_days';
import addDays from 'date-fns/add_days';
import subDays from 'date-fns/sub_days';
import isWithinRange from 'date-fns/is_within_range';

import { apiUrl } from '../config';
import { isFutureTrip, isPastTrip } from '../helpers/dateUtils';

import Spinner from './Spinner.react';
import TripCard from './TripCard.react';
import DateRange from './DateRange.react';

// Custom Icon for the leaftlet map.
const locationIcon = divIcon({
  html: '<span class="icono-caretDown"/>',
  iconSize: [25, 25],
  iconAnchor: [17, 24],
  popupAnchor: [-3, -26]
});

const LocationMap = ({ userId }) => {
  const [locations, setLocations] = useState(null);
  const [activeLocation, setActiveLocation] = useState(null);
  const [dateSelection, setDateSlider] = useState(new Date());

  const fetchUserLocations = id => {
    if (!id) {
      return;
    }
    setLocations(null);
    return axios(`${apiUrl}/u/${id}/locations/`, {
      withCredentials: true
    }).then(res => {
      if (res.status === 200) {
        setLocations(res.data.data);
        setDateSlider(dateFromPercentage(50, res.data.data));
      } else if (res.status === 404) {
        setLocations([]);
      }
    });
  };

  useEffect(
    () => {
      fetchUserLocations(userId);
    },
    [userId]
  );

  if (locations === null) {
    return <Spinner />;
  }

  const dateSpread = Math.floor(getDaysInRange(locations) / 4);

  // Sort so the soonest trip is first in the list
  locations.sort((a, b) => differenceInDays(a.start_date, b.start_date));

  // Only show locations +/- 60 days from date
  const filteredLocations = locations.filter(
    isWithinDaysN(dateSelection, dateSpread)
  );

  const fLocations = filteredLocations.filter(isFutureTrip);
  const pLocations = filteredLocations.filter(isPastTrip);

  const bounds =
    filteredLocations.length > 1
      ? filteredLocations.map(getLatLong)
      : locations.map(getLatLong);

  return (
    <Fragment>
      <Map bounds={findBoundingBox(bounds)} style={{ height: 300 }}>
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a>'
        />
        <Polyline positions={pLocations.map(getLatLong)} color="black" />
        <Polyline positions={fLocations.map(getLatLong)} color="#bf4e4e" />
        {filteredLocations.map(({ id, city, start_date, end_date }) => (
          <Marker
            key={id}
            position={[city.latitude, city.longitude]}
            icon={locationIcon}
          >
            <Popup
              onOpen={() => setActiveLocation(id)}
              onClose={() => setActiveLocation(null)}
              className="uk-hidden"
            />
          </Marker>
        ))}
      </Map>
      <input
        className="uk-range"
        type="range"
        onChange={e =>
          setDateSlider(dateFromPercentage(e.target.value, locations))
        }
        value={percentageFromDate(dateSelection, locations)}
      />
      <label style={{ margin: 'auto' }}>
        <DateRange
          className="uk-flex-around"
          hideDaysCount
          start_date={subDays(dateSelection, dateSpread)}
          end_date={addDays(dateSelection, dateSpread)}
        />
      </label>
      <hr />
      {!activeLocation && <p>No trip selected</p>}
      {activeLocation && (
        <TripCard viewOnly {...locations.find(l => l.id === activeLocation)} />
      )}
    </Fragment>
  );
};

const getDaysInRange = locations => {
  if (locations.length === 0) {
    return 0;
  }
  return differenceInDays(
    locations[locations.length - 1].end_date,
    locations[0].start_date
  );
};

const percentageFromDate = (date, locations) => {
  const daysInRange = getDaysInRange(locations);
  const daysFromStart = differenceInDays(date, locations[0].start_date);
  return Math.round((daysFromStart / daysInRange) * 100);
};

const dateFromPercentage = (percentage, locations) => {
  const daysInRange = getDaysInRange(locations);
  const daysFromStart = Math.round(daysInRange * (percentage / 100));
  return addDays(locations[0].start_date, daysFromStart);
};

const getLatLong = ({ city }) => [city.latitude, city.longitude];

const findBoundingBox = bounds => [
  [
    Math.min.apply(null, bounds.map(b => b[0])) - 2,
    Math.min.apply(null, bounds.map(b => b[1])) - 2
  ],
  [
    Math.max.apply(null, bounds.map(b => b[0])) + 2,
    Math.max.apply(null, bounds.map(b => b[1])) + 2
  ]
];

const isWithinDaysN = (dateSelection, dateSpread) => ({
  start_date,
  end_date
}) => {
  return (
    isWithinRange(
      start_date,
      subDays(dateSelection, dateSpread),
      addDays(dateSelection, dateSpread)
    ) ||
    isWithinRange(
      end_date,
      subDays(dateSelection, dateSpread),
      addDays(dateSelection, dateSpread)
    )
  );
};

export default LocationMap;
