//
// Page with a list of trips for the user
//
import React, { Fragment, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import {
  fetchTrips,
  fetchCities,
  createTrip,
  fetchFriends,
  updateTrip,
  deleteTrip
} from '../actions';

import differenceInDays from 'date-fns/difference_in_days';
import isBefore from 'date-fns/is_before';
import {
  isFutureTrip,
  isPastTrip,
  determineAlreadySelectedTripDates
} from '../helpers/dateUtils';

import NewTrip from '../components/NewTrip.react';
import TripCard from '../components/TripCard.react';
import Spinner from '../components/Spinner.react';

function TripDashboard(props) {
  // Get data out of redux
  const { cities, trips, users } = props;

  const [isShowingPastTrips, showPastTrips] = useState(false);

  // Load API data for page
  useEffect(() => {
    if (!cities.isFetching && !cities.hasFetched) {
      props.fetchCities();
    }
    if (!trips.isFetching && !trips.hasFetched) {
      props.fetchTrips();
    }
  }, []);

  const togglePastTrips = () => {
    if (!isShowingPastTrips) {
      props.fetchTrips({
        end_date_after: new Date('2018-01-01')
      });
    }
    showPastTrips(!isShowingPastTrips);
  };

  const populatedTrips = trips.ids
    .map(tId => {
      const trip = trips.all[tId];
      return {
        ...trip,
        city: cities.all[trip.city],
        matches: trip.matches.map(match => {
          const [uId, start, end] = match.split('::');
          return {
            ...users.all[uId],
            start_date: start,
            end_date: end
          };
        })
      };
    })
    .filter(trip => isShowingPastTrips || isBefore(new Date(), trip.end_date));

  // Sort so the soonest trip is first in the list
  populatedTrips.sort((a, b) => differenceInDays(a.start_date, b.start_date));

  const disabledDates = determineAlreadySelectedTripDates(populatedTrips);

  return (
    <Fragment>
      <h4>ADD NEW TRIP</h4>
      <NewTrip
        cities={cities}
        trips={populatedTrips}
        isLoadingCities={cities.isFetching}
        onCreateTrip={props.createTrip}
        disabledDates={disabledDates}
      />

      <TripsList
        isFetching={trips.isFetching}
        trips={populatedTrips}
        updateTrip={props.updateTrip}
        deleteTrip={props.deleteTrip}
        disabledDates={disabledDates}
      />

      <button
        className="uk-button uk-button-secondary"
        onClick={togglePastTrips}
      >
        {isShowingPastTrips ? 'Hide Past Trips' : 'Show Past Trips'}
      </button>
    </Fragment>
  );
}

function TripsList({
  isFetching,
  trips,
  disabledDates,
  updateTrip,
  deleteTrip
}) {
  if (isFetching) {
    return <Spinner />;
  }

  if (trips.length === 0) {
    return <p>You have no upcoming trips. Hope you enjoy your rest.</p>;
  }

  // TODO: Pagination

  const fTrips = trips.filter(isFutureTrip);
  const pTrips = trips.filter(isPastTrip);

  return (
    <Fragment>
      {pTrips.length > 0 && <h4>PAST TRIPS</h4>}
      {pTrips.map(trip => (
        <TripCard
          key={trip.id}
          onUpdate={updateTrip}
          onDelete={deleteTrip}
          disabledDates={disabledDates}
          {...trip}
        />
      ))}
      {fTrips.length > 0 && <h4>UPCOMING TRIPS</h4>}
      {fTrips.map(trip => (
        <TripCard
          key={trip.id}
          onUpdate={updateTrip}
          onDelete={deleteTrip}
          disabledDates={disabledDates}
          {...trip}
        />
      ))}
    </Fragment>
  );
}

const mapStateToProps = ({ cities, trips, users }) => ({
  cities,
  trips,
  users
});

export default connect(
  mapStateToProps,
  {
    fetchTrips,
    fetchCities,
    fetchFriends,
    createTrip,
    updateTrip,
    deleteTrip
  }
)(TripDashboard);
