import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import ReactCountryFlag from 'react-country-flag';

import FriendChip from './FriendChip.react';

import differenceInDays from 'date-fns/difference_in_days';
import getOverlappingDaysInRanges from 'date-fns/get_overlapping_days_in_ranges';
import format from 'date-fns/format';

const TripPanel = ({ city, start_date, end_date, matches, isActive }) => (
  <dl className="uk-description-list">
    <dt>Location</dt>
    <dd className="uk-padding-small">
      <span className="uk-padding-small">{city.name}</span>
      <ReactCountryFlag code={city.country_code} svg />
    </dd>
    <dt>Duration</dt>
    <dd className="uk-padding-small">
      <span className="uk-padding">
        You will be in {city.name} for {differenceInDays(end_date, start_date)}{' '}
        Days
      </span>
      <OverlapBar
        start_date={start_date}
        end_date={end_date}
        to={end_date}
        from={start_date}
      />
    </dd>
    <OverlappingFriends
      matches={matches}
      start_date={start_date}
      end_date={end_date}
    />
  </dl>
);

const OverlappingFriends = ({ matches, start_date, end_date }) => {
  if (matches.length === 0) {
    return (
      <p>
        None of your friends trips are currently overlapping with this one.
        Maybe you need to add some people you know to improve your chances?
      </p>
    );
  }

  return (
    <Fragment>
      <dt>Friends with overlapping dates</dt>
      <dd>
        <span className="uk-padding">
          {matches.map(m => (
            <OverlapGraph key={m.id} {...m} from={start_date} to={end_date} />
          ))}
        </span>
      </dd>
    </Fragment>
  );
};

const OverlapGraph = ({
  id,
  first_name,
  last_name,
  username,
  photo_url,
  start_date,
  end_date,
  from,
  to
}) => (
  <div className="overlap-graph">
    <div className="uk-flex uk-flex-middle">
      <FriendChip
        id={id}
        username={username}
        first_name={first_name}
        last_name={last_name}
        photo_url={photo_url}
        photoOnly
      />
      <Link to={`/users/${id}`} className="chip-label uk-link-muted">
        @{username}
      </Link>
    </div>
    <OverlapBar
      start_date={start_date}
      end_date={end_date}
      to={to}
      from={from}
    />
  </div>
);

const OverlapBar = ({ id, start_date, end_date, from, to }) => {
  const totalLength = differenceInDays(to, from);
  const daysFromStart = differenceInDays(start_date, from);
  const daysOverlap = getOverlappingDaysInRanges(
    from,
    to,
    start_date,
    end_date
  );

  const STYLES = {
    width: `${Math.floor((daysOverlap * 80) / totalLength)}%`
  };
  const START_STYLES = {
    marginLeft: `${Math.max(
      Math.floor((daysFromStart * 80) / totalLength) - 5,
      0
    )}%`
  };

  // TODO: This look gross ATM
  return null;
  return (
    <div className="uk-flex uk-flex-around uk-padding-small">
      <span className="date-icon" style={START_STYLES}>
        {format(start_date, 'DD MMM')}
      </span>
      <li className="uk-list overlap-bar" style={STYLES}>
        {daysOverlap} Days
      </li>
      <span className="date-icon">{format(end_date, 'DD MMM')}</span>
    </div>
  );
};

export default TripPanel;
