import React, { Fragment, useEffect } from 'react';
import { connect } from 'react-redux';
import {
  fetchFriends,
  requestFriendship,
  acceptFriendRequest
} from '../actions';

import Spinner from '../components/Spinner.react';
import FriendChip from '../components/FriendChip.react';
import FriendSearch from '../components/FriendSearch.react';
import FriendStatusButton from '../components/FriendStatusButton.react';

const FriendsList = ({
  users,
  fetchFriends,
  requestFriendship,
  acceptFriendRequest,
  activeUser
}) => {
  useEffect(() => {
    if (!users.isFetching) {
      fetchFriends();
    }
  }, []);

  return (
    <Fragment>
      <MainPanel
        isFetching={users.isFetching && users.ids.length === 0}
        users={users}
        activeUser={activeUser}
        acceptFriendRequest={acceptFriendRequest}
        requestFriendship={requestFriendship}
      />
    </Fragment>
  );
};

const MainPanel = ({
  isFetching,
  users,
  activeUser,
  acceptFriendRequest,
  requestFriendship
}) => {
  if (isFetching) {
    return <Spinner />;
  }

  const friendList = Object.keys(users.all)
    .filter(uId => parseInt(uId, 10) !== activeUser)
    .map(uId => users.all[uId]);

  const accepted = friendList.filter(f => f.is_friend);
  const hasFriends = accepted.length > 0;
  const pending = friendList.filter(f => f.is_pending_friend);
  const hasPendingFriends = pending.length > 0;

  return (
    <Fragment>
      {hasFriends && <h4>FRIENDS</h4>}
      {accepted.map(f => (
        <FriendChip key={f.id} {...f} />
      ))}
      {hasPendingFriends && <h4>PENDING REQUESTS</h4>}
      {pending.map(f => (
        <Fragment key={f.id}>
          <FriendChip {...f} onAccept={() => acceptFriendRequest(f)} />
          <FriendStatusButton
            user={f}
            onAccept={() => acceptFriendRequest(f)}
          />
        </Fragment>
      ))}
      <h4>FIND FRIENDS</h4>
      <FriendSearch
        users={users}
        onRequestFriend={requestFriendship}
        onAcceptFriend={acceptFriendRequest}
      />
    </Fragment>
  );
};

const mapStateToProps = ({ users, activeUser }) => ({
  users,
  activeUser
});

export default connect(
  mapStateToProps,
  {
    fetchFriends,
    requestFriendship,
    acceptFriendRequest
  }
)(FriendsList);
