import React, { Fragment, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { fetchCities, fetchExploreSuggestions } from '../actions';

import addDays from 'date-fns/add_days';
import format from 'date-fns/format';

import Select from 'react-select';
import Flatpickr from 'react-flatpickr';
import ReactCountryFlag from 'react-country-flag';

import Spinner from '../components/Spinner.react';
import FriendChip from '../components/FriendChip.react';

const CONTINENT_OPTIONS = [
  { label: 'Europe', value: 'EU' },
  { label: 'North America', value: 'NA' },
  { label: 'South America', value: 'SA' },
  { label: 'Africa', value: 'AF' },
  { label: 'Asia', value: 'AS' },
  { label: 'Oceania', value: 'OC' }
];

const Explore = ({
  cities,
  suggestions,
  users,
  fetchCities,
  activeUser,
  fetchExploreSuggestions
}) => {
  const [continent, setContinent] = useState(null);
  const [dates, setDates] = useState([new Date(), addDays(new Date(), 7)]);

  useEffect(() => {
    if (!cities.isFetching && cities.ids.length === 0) {
      fetchCities();
    }
  }, []);

  useEffect(
    () => {
      if (continent && dates[0] && dates[1]) {
        fetchExploreSuggestions({
          continent: continent.value,
          start_date: format(dates[0], 'YYYY-MM-DD'),
          end_date: format(dates[1], 'YYYY-MM-DD')
        });
      }
    },
    [continent, dates[0], dates[1]]
  );

  if (suggestions.isFetching) {
    return <Spinner />;
  }

  const populatedSuggestions = suggestions.ids.map(sId => {
    const s = suggestions.all[sId];
    const us = s.users.map(uId => users.all[uId]);
    return {
      city: cities.all[s.city],
      users: us
    };
  });

  return (
    <Fragment>
      <h4>FILTERS</h4>
      <div className="uk-flex uk-flex-around">
        <Select
          styles={{
            container: () => ({
              width: 250
            }),
            menu: provided => ({
              ...provided,
              top: null
            })
          }}
          placeholder="Continent"
          className="contient-picker"
          isSearchAble
          isLoading={suggestions.isFetching}
          value={continent}
          onChange={setContinent}
          options={CONTINENT_OPTIONS}
        />
        <Flatpickr
          className="uk-input uk-width-1-3@m"
          options={{
            mode: 'range',
            dateFormat: 'Y-m-d'
          }}
          value={dates}
          onChange={setDates}
        />
      </div>
      <h4>SUGGESTIONS</h4>
      <SuggestionList suggestions={populatedSuggestions} />
    </Fragment>
  );
};

const SuggestionList = ({ suggestions }) => {
  if (suggestions.length === 0) {
    return (
      <div>
        No suggested trips for these filters... Try another continent or date
        range.
      </div>
    );
  }
  return (
    <div>
      {suggestions.map(({ city, users }) => (
        <div
          key={city.id}
          className="uk-tile uk-padding-small uk-flex uk-flex-between uk-link-muted"
        >
          <h6>
            <ReactCountryFlag code={city.country_code} svg /> | {city.name}
          </h6>
          {users.map(u => <FriendChip key={u.id} {...u} />)}
        </div>
      ))}
    </div>
  );
};

const mapStateToProps = ({ cities, suggestions, users, activeUser }) => ({
  cities,
  suggestions,
  users,
  activeUser
});

export default connect(
  mapStateToProps,
  {
    fetchCities,
    fetchExploreSuggestions
  }
)(Explore);
