import React from 'react';

const TelegramLink = ({ username }) => {
  if (!username) {
    return null;
  }
  return (
    <a
      className="uk-button telegram-link"
      href={`https://t.me/${username}`}
      target="_blank"
      rel="no-opener"
    >
      <img src="/telegram.svg" alt="telegram-icon" />
      <span>Send Message</span>
    </a>
  );
};

export default TelegramLink;
