import React from 'react';
import { Link } from 'react-router-dom';

const FriendChip = ({
  id,
  first_name,
  last_name,
  username,
  photo_url,
  photoOnly
}) => {
  const photoSrc = photo_url || '/blank-profile-picture.png';
  const tooltipText = username || `${first_name} ${last_name}`;

  if (photoOnly) {
    return (
      <div className="uk-border-circle friend-chip">
        <img
          className="uk-border-circle avatar-preview"
          alt="avatar"
          src={photoSrc}
        />
      </div>
    );
  }

  return (
    <Link className="uk-border-circle friend-chip" to={`/users/${id}`}>
      <img
        className="uk-border-circle avatar-preview"
        alt="avatar"
        src={photoSrc}
      />
      <div className="uk-badge tooltip">{tooltipText}</div>
    </Link>
  );
};

export default FriendChip;
