import React, { useState, useEffect, Fragment } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';

import { apiUrl } from '../config';

import FriendChip from './FriendChip.react';
import FriendStatusButton from './FriendStatusButton.react';

// TODO:
//  - integrate with search API
//  - present results effectively
//  - request button
function FriendSearch({
  users,
  onRequestFriend,
  onAcceptFriend,
  fetchFriends
}) {
  const [results, setResults] = useState([]);
  const [phrase, setPhrase] = useState('');

  // Each time the user types, we send a search query.
  //
  // TODO:
  // - Should this be debounced?
  // - Encoding query strings
  //
  useEffect(
    () => {
      const CancelToken = axios.CancelToken;
      const source = CancelToken.source();

      if (phrase) {
        axios
          .get(`${apiUrl}/u/search/?q=${phrase}`, {
            cancelToken: source.token,
            withCredentials: true
          })
          .then(({ data }) => setResults(data.data))
          .catch(err => {
            // All good.
            if (err && err.message === 'unmount') {
              return;
            }

            // TODO: Handle this!
            console.log(err);
          });
      } else {
        setResults([]);
      }

      // Cancel the request if the component is unmounted
      return () => source.cancel('unmount');
    },
    [phrase]
  );

  return (
    <div className="uk-padding-small">
      <input
        className="uk-input"
        placeholder="Search for Telegram Username / Full Name"
        value={phrase}
        onChange={e => setPhrase(e.target.value)}
      />
      <hr />
      <dl className="uk-description-list">
        {results.map(r => {
          // Get friend object so we can tell if we're friends with each search result
          const friendObj = users.all[r.id];

          // Don't show yourself in search results
          if (friendObj && friendObj.is_you) {
            return null;
          }

          return (
            <Fragment key={r.id}>
              <dt>{r.username}</dt>
              <dd className="uk-padding-small uk-flex uk-flex-between">
                <span className="uk-flex">
                  <FriendChip
                    {...r}
                    photoOnly={!friendObj || !friendObj.is_friend}
                  />
                  <p>
                    {r.first_name} {r.last_name}
                  </p>
                </span>
                <span className="uk-flex">
                  <hr
                    style={{ height: 30 }}
                    className="uk-divider-vertical uk-margin-right uk-margin-left"
                  />
                  <FriendStatusButton
                    user={friendObj}
                    onAccept={() => onAcceptFriend(r)}
                    onRequest={() => onRequestFriend(r)}
                  />
                </span>
              </dd>
            </Fragment>
          );
        })}
      </dl>
    </div>
  );
}

const mapStateToProps = ({ users }) => ({
  users
});

export default connect(
  mapStateToProps,
  {}
)(FriendSearch);
